'use client';

import "./globals.css";
import styles from "./layout.module.css";
import Link from "next/link";
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  // Loader animado ao trocar de página
  const pathname = typeof window !== "undefined" ? window.location.pathname : "";
  const [loading, setLoading] = useState(false);
  const [currentPath, setCurrentPath] = useState(pathname);

  useEffect(() => {
    const handleStart = () => setLoading(true);
    const handleStop = () => setLoading(false);
    if (typeof window !== "undefined") {
      const onPopState = () => {
        setLoading(true);
        setTimeout(() => setLoading(false), 600);
      };
      window.addEventListener("popstate", onPopState);
      return () => window.removeEventListener("popstate", onPopState);
    }
  }, []);
  useEffect(() => {
    if (typeof window !== "undefined") {
      if (currentPath !== window.location.pathname) {
        setLoading(true);
        setTimeout(() => {
          setLoading(false);
          setCurrentPath(window.location.pathname);
        }, 600);
      }
    }
  }, [children]);

  return (
    <html lang="pt-BR">
      <body>
        {loading && (
          <div className="loader-bg">
            <div className="loader-spinner" />
          </div>
        )}
        <nav className={styles.navbar}>
          <ul className={styles.menu}>
            <li><Link href="/">Home</Link></li>
            <li><Link href="/sobre">A Pousada 3 Palmeiras</Link></li>
            <li><Link href="/acomodacoes">Acomodações</Link></li>
            <li><Link href="/politica-cancelamento">Política de Cancelamento de Reserva</Link></li>
            <li><Link href="/contato">Contato</Link></li>
          </ul>
        </nav>
        <main>{children}</main>
      </body>
    </html>
  );
}
